.wrapper {
    min-height: 2440px;
}
.shop_sidebar {
    &_menu {
        min-height: 445px;
        margin-bottom: 35px;
    }
    &_price {
        min-height: 105px;
        margin-bottom: 35px;
        border: 1px solid #252525;
        h4 {
            color: #1c1c1c;
            font-weight: 700;
            margin-bottom: 25px;
            font-size: 24px;
        }
    }
    &_color {
        min-height: 150px;
        margin-bottom: 35px;
        h4 {
            color: #1c1c1c;
            font-weight: 700;
            margin-bottom: 25px;
            font-size: 24px;
        }
        .color_white {
            width: 50%;
            display: inline-block;
            margin-bottom: 10px;
            label {
                position: relative;
                span {
                    margin-left: 30px;
                }
                input {
                    display: none;
                }
                &:after {
                    position: absolute;
                    left: 0;
                    top: 7px;
                    height: 14px;
                    width: 14px;
                    background: #cdcbcb;
                    content: '';
                    border-radius: 50%;
                }
            }
        }
