        h4 {
            color: #1c1c1c;
            font-weight: 700;
            margin-bottom: 25px;
            font-size: 24px;
        }
        .sidebar__item__size {
            display: inline-block;
            margin-right: 16px;
            margin-bottom: 10px;
            label {
                font-size: 12px;
                color: #6f6f6f;
                display: inline-block;
                padding: 8px 25px 6px;
