        h4 {
            color: #1c1c1c;
            font-weight: 700;
            margin-bottom: 25px;
            font-size: 24px;
        }
        .shop_sidebar_product_item {
            display: flex;
            margin-top: 20px;
            img {
                margin-right: 20px;
            }
        }
        .latest_product__item__text {
            padding-top: 10px;
            h6 {
                color: #252525;
                margin-bottom: 8px;
                font-size: 16px;
                font-weight: 400;
            }
            span {
                font-size: 18px;
                display: block;
                color: #252525;
                font-weight: 700;
            }
        }
    }
}
.hero_title__name {
    color: #1c1c1c;
    font-weight: 700;
    margin-bottom: 25px;
    font-size: 24px;
}
.hero_title__list {
    li {
        font-size: 16px;
        color: #1c1c1c;
        line-height: 39px;
        display: block;
    }
}
